<template>
  <div>
      <userRot
        :list="RotationList"
        />
      <div class="CardBox">
        <div  v-for="(item,index) in TNews" :key="item.id">
            <Card
            :img="solid_pictures[testNumber(index)].src"
            :words="item.title"
            :NewsClass="item.origin"
            :NewsTime="item.releaseTime.slice(0,-9)"
            :url="item.id"
            :Top="item.status"
            />
        </div>
        <div  v-for="(item,index) in News" :key="item.id" v-show="item.filter===filter||filter===''">
            <Card
            :img="solid_pictures[testNumber(index)].src"
            :words="item.title"
            :NewsClass="item.origin"
            :NewsTime="item.releaseTime"
            :url="item.id"
            :Top="item.status"
            />
        </div>
      </div>
  </div>
</template>

<script>
import userRot from '@/components/twtRotation.vue'
import Card from '@/components/Card.vue'
import global from '@/global.vue'
export default 
{
    props:{
      filter: {
            type: String,
            default: ""
        },
    },
    data() 
    {
      return {
        RotationList: [
                      {
                          imgPath: 'https://s1.328888.xyz/2022/08/18/3cV30.jpg',
                          url: '',
                          header: "天津大学欢迎你",
                          time: `2022/8/18`,
                      },
                      {
                          imgPath: 'https://s1.328888.xyz/2022/08/18/3csMp.jpg',
                          url: '',
                          header: "入住天大“新家”",
                          time: `2022/8/18`,
                      },
                      {
                          imgPath: 'https://s1.328888.xyz/2022/08/18/3cMXo.jpg',
                          url: '',
                          header: "校长开学致辞",
                          time: `2022/8/18`,
                      },
                      {
                          imgPath: 'https://s1.328888.xyz/2022/08/18/3Cmrr.jpg',
                          url: '',
                          header: "活力全开军旅体验",
                          time: `2022/8/18`,
                      },
                  ],
        News:[],
        TNews:[],
        solid_pictures:[
          {src:"https://s1.328888.xyz/2022/08/18/3ccah.jpg"},
          {src:"https://s1.328888.xyz/2022/08/18/3c2is.jpg"},
          {src:"https://s1.328888.xyz/2022/08/18/3coxn.png"}
        ]
      }
    },
    components:
    {
      userRot,
      Card,
      global
    },
    methods:
    {
      testNumber(n){
        if(n % 3 == 0)return 0;
        else if(n % 2 == 0) return 1;
        else return 2;
      }
    },
    created()
    {
      fetch("http://8.130.96.252/art/select/summary/nor/")
      .then(res=>res.json())
      .then(res=>{
        this.News = res.result;
        /*手动添加了每个子元素的过滤器*/
        for(let a = 0; a<this.News.length;a++) {
          this.News[a].filter = "2";
          this.News[a].releaseTime = this.News[a].releaseTime.split(" ")[0];
        }
      })

      fetch("http://8.130.96.252/art/select/summary/top/")
      .then(res=>res.json())
      .then(res=>{
        this.TNews = res.result;
        console.log(this.News);
      })
    },
}
</script>

<style scoped>
.CardBox{
  margin-left: 15%;
  margin-top: 3%;
  width: 80%;
  height: 60vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  
}
</style>