<template>
    <div class="rotation">
       <div class="carouselBox"
         @mouseenter.stop="handleMouseEnter"
         @mouseleave.stop="handleMouseLeave"
    >
        <!--图片数组-->
        <div v-for="(item,index) in list" :key="index">
            <transition name="slide-right">
                <div @click="goto(item.url)" v-show="index === activeIndex" class="carouselContent">
                    <img :src="item.imgPath" class="R-img">
                </div>
            </transition>
            <div v-show="index === activeIndex" class="textContent">
                <div class="header">
                    {{item.header}}
                </div>
                <div class="time">
                    {{item.time}}
                </div>
            </div>
        </div>
        <!--指示器-->
        <div class="carousel__indicatorsBox"
             v-if="!hideIndicator"
        >
            <div v-for="(item, index) in list" :key="index"
                 :class="['carousel__indicator',{ 'activeIndicator': index === activeIndex }]"
                 @mouseenter="indicatorHover(index)"
                 @click.stop="indicatorClick(index)">
            </div>
        </div>
    </div>
</div>
    
</template>
<style scoped>
.R-img{
    width: 55%;
}

.rotation{
    width: 100%;
    height: 30vh;
}
.textContent{
        position: absolute;
        left: 50%;
        width: 50%;
        height: 100%;
        background-color:skyblue;
}
.textContent>div{
    color: white;
}
.textContent .header{
    font-size: 25px;
    margin-top: 10%;
    margin-left: 10%;
}

.textContent .time{
    font-size: 15px;
    margin-left: 10%;
}

</style>
<script>
   export default {
        props: {
            // 是否隐藏指示器
            hideIndicator: Boolean,
            // 轮播图切换时间间隔，单位毫秒ms
            interval: {
                type: Number,
                default: 1000
            },
            // 是否循环播放
            loop: {
                type: Boolean,
                default: true
            },
            // 是否自动播放
            autoplay: {
                type: Boolean,
                default: true
            },
            // 图片数据——对象数组 imgPath属性为图片路径，url属性为点击跳转的链接
            list: Array
        },
        data() {
            return {
                activeIndex: 0,
                style: {
                    'height': this.height,
                    'width': this.width
                },
                timer: null,
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.startTimer();
            });
        },
        methods: {
            // 鼠标移入轮播图——暂停轮播
            handleMouseEnter() {
                this.pauseTimer();
            },
            // 鼠标移出轮播图——重启轮播
            handleMouseLeave() {
                this.startTimer();
            },
            // 启动轮播
            startTimer() {
                if (this.interval <= 0 || !this.autoplay || this.timer) return;
                this.timer = setInterval(this.playSlides, this.interval);
            },
            // 暂停轮播
            pauseTimer() {
                if (this.timer) {
                    clearInterval(this.timer);
                    this.timer = null;
                }
            },
            // 切换展示的图片
            playSlides() {
                if (this.activeIndex < this.list.length - 1) {
                    this.activeIndex++;
                } else if (this.loop) {
                    this.activeIndex = 0;
                }
            },
            // 鼠标悬浮于指示器上时，切换到相应的图片
            indicatorHover(index) {
                this.activeIndex = index;
            },
            // 点击指示器时，切换到相应的图片
            indicatorClick(index) {
                this.activeIndex = index;
            },
            // 点击图片时，跳转到指定链接
            goto(url) {
                window.open(url)
            }
        }
    }
</script>
<style scoped>
    /*轮播图盒子样式 自动水平居中*/
    .carouselBox {
        position: relative;
        overflow: hidden;
        margin: -50px auto;
        height: inherit;
        width: 80%;
        border-radius: 25px;
        background-color: rgb(250, 250, 250);
    }
 
    /*子绝父相定位——使指示器定位在轮播图下边缘*/
    .carousel__indicatorsBox {
        position: absolute;
        display: flex;
        bottom: 10px;
        left: 80%;
        transform: translateX(-50%);
    }
 
    /*指示器样式*/
    .carousel__indicator {
        margin: 0px 6px;
        opacity: .48;
        width: 30px;
        height: 4px;
        background-color: rgb(47, 48, 48);
        border: none;
        outline: 0;
        cursor: pointer;
        -webkit-transition: .3s;
        transition: .3s;
    }
 
    /*鼠标悬浮于指示器上时，透明度变为1*/
    .activeIndicator {
        opacity: 1;
    }
 
    /*子绝父相定位——使图片层叠在轮播图中心*/
    .carouselContent {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: auto;
    }
 
    /*轮播图切换时的过渡动画 -- 从左滑动进入，从右滑动移出*/
    .slide-right-enter-active,
    .slide-right-leave-active {
        will-change: transform;
        transition: all 500ms;
    }
 
    .slide-right-enter {
        opacity: 0;
        transform: translateX(-100%);
    }
 
    .slide-right-leave-active {
        opacity: 0;
        transform: translateX(100%);
    }
</style>